.container{
    margin-top: 3.5vmax;
}


.heading{
    font-size: 2vmax !important;
    font-weight: bold !important;
    font-style: normal !important;
    font-family: 'Times New Roman', Times, serif !important;
}

.libContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   /* margin-top: 7%;*/
}

.libContainer > img{
    margin:1.5vmax;
    height: 15vmax;
}

.libContainer > div{
    margin: 1vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(59, 59, 59);
    font-size: 1.75vmax;
    font-weight: bold;
 
}

.libContainer > div > span{
    font-weight: bolder;
    margin-top: 1vmax;
    font-size: 1.5vmax;
    color: rgb(113, 104, 104);
    font-family: 'Courier New', Courier, monospace;
}

.info{
    font-family: cursive !important;
    font-size: 1.25vmax !important;
}

.blueClass{
    color: rgb(63, 63, 231) !important;
}

.inst{
    color: rgba(0, 0, 0, 0.781) !important;
    font-weight: 400 !important;
    font-size: 1.2vmax !important;
}

.instSpan{
    color: rgba(0, 0, 0, 0.83);
    font-size: 1.25vmax;
    font-weight: bold;
}