.logo{
  height: 3vmax;
  width: 3vmax;
}

.navbar {
  background: linear-gradient(90deg, rgba(28, 27, 27, 0) 0%, rgba(255, 255, 255, 0) 100%);
  height: 4vmax;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  margin-top: 2vmax;
}

.navbar-logo {
  color: #1a1a1a90;
  justify-self: start;
  margin-left: 1vmax;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.navbar > a{
  margin : 2vmax;
}

.navbar > a > button {
  appearance: none;
  background-color: #d4e5ed;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 0.4vmax;
  box-shadow: rgba(27, 31, 35, 0.04) 0 1px 0, rgba(255, 255, 255, 0.25) 0 1px 0 inset;
  box-sizing: border-box;
  color: #24292E;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, system-ui, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 1vmax;
  font-weight: bold;
  line-height: 1.35vmax;
  list-style: none;
  width: 10vmax;
  padding: 0.4vmax 0.7vmax;
  position: relative;
  transition: background-color 0.2s cubic-bezier(0.3, 0, 0.5, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
  word-wrap: break-word;
 }
 
 .navbar > a >button:hover {
  background-color: #F3F4F6;
  text-decoration: none;
  transition-duration: 0.1s;
 }
 
 .navbar > a >button:disabled {
  background-color: #FAFBFC;
  border-color: rgba(27, 31, 35, 0.15);
  color: #959DA5;
  cursor: default;
 }
 
 .navbar > a >button:active {
  background-color: #EDEFF2;
  box-shadow: rgba(225, 228, 232, 0.2) 0 1px 0 inset;
  transition: none 0s;
 }
 
 .navbar > a >button:focus {
  outline: 1px transparent;
 }
 
 .navbar > a >button:before {
  display: none;
 }
 
 .navbar > a >button:-webkit-details-marker {
  display: none;
 }