.detailsContainer{
    margin:10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
   /* align-items: center;*/
}

.detailsContainer>h2{
    font-family: cursive;
}

.buttonContainer{
    align-items:center;
    justify-content: center;
    padding: 2vmax;
    padding-left: 34.5vmax;
}

.details{
    margin: 0.6vmax;
}

.prefix{
    font-size: 1.2vmax;
    font-style: italic;
    font-weight: bold;
    color:rgb(61, 61, 61);
}

.value{
    font-size: 1.1vmax;
}

.booksToReturn{
    max-width: 25vmax;
}

.booksToReturn > a{
    text-decoration: none;
    color:rgb(61, 61, 61);
}


.books{
    background-color: rgb(230, 234, 234);
    margin: 0 5%;
    margin-bottom: 2.5vmax;
    padding-bottom: 1vmax;
}

.fees{
    background-color: rgb(230, 234, 234);
    padding: 1vmax;
    border-radius: 2%;
    margin-bottom: 2.5vmax;
}

.heading{
    font-size: 1.1vmax;
    font-style: italic;
    font-family: cursive;
    margin: 0.5% 5%;
}

.spanD{
    font-size:1.1vmax;
    font-style: italic;
    font-weight: bold;
    color: rgb(65, 65, 65);
}
.spanFine{
    color:rgb(222, 62, 62);
    font-weight: bold;
    font-size: 1.1vmax;
}


.spanPaid{
    color:rgb(58, 212, 73);
    font-weight: bold;
    font-size: 1.1vmax;
}



.fineBtn {
  background-color: initial;
  background-image: linear-gradient(-180deg, #FF7E31, #E62C03);
  border-radius: 0.4vmax;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0.15vmax 0.3vmax;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 1vmax;
  font-family: Inter,-apple-system,system-ui,Roboto,"Helvetica Neue",Arial,sans-serif;
  height: 2.2vmax;
  outline: 0;
  overflow: hidden;
  position: relative;
  text-align: center;
  width: 25%;
  z-index: 9;
  border: 0;
  transition: all linear .2s;
  padding: 0 0.4vmax;
  margin-left:8vmax;
/*  margin-bottom: 1vmax;*/
}

.fineBtn:hover {
  box-shadow: rgba(253, 76, 0, 0.5) 0 0.23vmax 0.68vmax;
}

.feeBtn{
    font-size: 1.1vmax;
    padding: 0.5em 2em;
    border: transparent;
    box-shadow: 2px 2px 4px rgba(0,0,0,0.4);
    background: dodgerblue;
    color: white;
    border-radius: 4px;
    transition: all 0.3s;
    cursor: pointer;
}

.feeBtn:hover {
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(30,144,255,1) 0%, rgba(0,212,255,1) 100%);
}

.feeBtn:active {
    transform: translate(0em, 0.2em);
   }


.headingb{
    font-size: 1.1vmax;
    font-style: italic;
    font-family: cursive;
    margin: 0.5% 5%;
}   
.greenClasss{
    font-size: 1.1vmax;
    font-style: italic;
    font-family: cursive;
    margin: 1% 5%;
    padding-top: 0.7vmax;
    color: rgb(16, 224, 16);
}
.redClasss{
    font-size: 1.1vmax;
    font-style: italic;
    font-family: cursive;
    margin: 1% 5%;
