.sidebar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 2vmax;
}
.sidebar > a{
    margin-left: 1vmax ;
    margin-right: 1vmax;
}


.sidebar > a > button {
    font-family: monospace;
    background-color: #f3f7fe;
    color: #2b68cb;
    border: none;
    border-radius: 0.5vmax;
    min-width: 8.5vmax;
    height: 2vmax;
    transition: .3s;
    font-size: 1vmax;
    font-weight: 600;
    white-space: nowrap;
    overflow:hidden;
  }
  
  .sidebar > a > button:hover {
    background-color: #3b82f6;
    box-shadow: 0 0 0 5px #3b83f65f;
    color: #fff;
    cursor:pointer;
  }
  
  

  